<template>
  <div id="editor">
    <el-container>
      <el-header height="36px">
        <el-button-group>
          <el-button
              type="primary"
              size="medium"
              class="el-icon-top-left"
              @click="handleImportButton"
          >
            
            <fileLoader id="fileLoader" @loadFile="loadBpmnFromFile"/>
          </el-button>
          <el-dropdown>
            <el-button type="primary" size="medium">
              
              <i class="el-icon-bottom-right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <a @click="downloadBpmnToFile">.bpmn</a>
              </el-dropdown-item>
              <el-dropdown-item>
                <a @click="downloadBpmnToSVG">.svg</a>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-button-group>
        <el-button-group>
          <!--
          <el-button
            type="primary"
            size="medium"
            class="el-icon-magic-stick"
            @click="textTransform"
          >NLP</el-button>
          -->
        </el-button-group>
        <!--style="margin-left:10px"-->
        <el-button-group>
          <el-button type="primary" size="medium" @click="resetBPMN"></el-button>
          <el-button type="primary" size="medium" @click="checkBPMN"></el-button>
<!--          <el-button type="primary" size="medium" @click="saveBPMN"></el-button>-->
<!--          <el-button type="primary" size="medium" @click="recoBPMN"></el-button>-->
<!--          <el-button type="primary" size="medium" @click="showWindow"></el-button>-->
<!--          <el-button type="primary" size="medium" @click="showUploadWindow"></el-button>-->
          <!--<el-button type="primary" size="medium" @click="abc"></el-button>-->
          <el-button type="primary" size="medium" @click="pmrun"></el-button>
          <el-button type="primary" size="medium" @click="showpopup">api</el-button>
        </el-button-group>
      </el-header>
      <el-dialog title="" :visible.sync="dialogFormVisible" center>
        <el-form ref="form" label-width="80px">
          <div
              class="binding-item"
              v-for="(item, index) in batchForm"
              :key="index"
          >
            <el-row>
              <el-col :span="8">
                <el-form-item label="">
                  <el-input
                      v-model="item.name"
                      placeholder=""
                      width="120px"
                      style="display:inline"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="">
                  <el-input
                      v-model="item.value"
                      placeholder=""
                      width="120px"
                      style="display:inline"
                  ></el-input>
                </el-form-item>
              </el-col>

              <!--              api-->
              <el-col :span="8">
                <el-form-item label=":">
                  <div>{{ item.type }}</div>
                </el-form-item>
              </el-col>

              <el-col :span="8">
                <el-form-item label="">
                  <div style="width: 300px">{{ item.info }}</div>
                </el-form-item>
              </el-col>

              <!--              <el-col :span="2">-->
              <!--                <el-form-item label-width="10px"  v-if="batchForm[index].index === batchFormNum">-->
              <!--                  <el-button type="primary" icon="el-icon-edit"  @click="batchAdd(index, batchForm[batchForm.length - 1].index)"></el-button>-->
              <!--                <span-->
              <!--                        class="el-icon-circle-plus binding-button-plus"-->
              <!--                        @click="batchAdd(index, batchForm[batchForm.length - 1].index)"-->
              <!--                ></span>-->
              <!--                </el-form-item>-->
              <!--              <el-col :span="2">-->
              <!--                <el-form-item>-->
              <!--                  &lt;!&ndash;                <el-form-item   v-if="item.name != '' || item.value!==''">&ndash;&gt;-->
              <!--                  <el-button type="danger" icon="el-icon-delete"  @click="batchDel(index)"></el-button>-->
              <!--&lt;!&ndash;                  <span&ndash;&gt;-->
              <!--&lt;!&ndash;                          class="el-icon-circle-close binding-button-close"&ndash;&gt;-->
              <!--&lt;!&ndash;                          @click="batchDel(index)"&ndash;&gt;-->
              <!--&lt;!&ndash;                  ></span>&ndash;&gt;-->
              <!--                </el-form-item>-->
              <!--              </el-col>-->
            </el-row>
          </div>
        </el-form>
        <el-button type="primary" size="medium" class="el-icon-check" @click="checknature"></el-button>
        <el-button type="primary" size="medium" class="el-icon-refresh" @click="resetForm"></el-button>
        <el-button type="primary" size="medium" @click="plan"></el-button>
<!--        <el-button type="primary" size="medium" @click="pmplan">2</el-button>-->
        <el-button type="danger" size="medium" @click="dialogFormVisible = false"></el-button>
      </el-dialog>
      <el-main>
        <!--  //&#45;&#45;
        <div class="righttop">
          <a class="rule" href="javascript:;" @click="showpopup"></a>
        </div>-->
        <!--        -->
        <div v-show="popup">
          <!---->
          <Search @childFn="recevieChildParam" v-on:transferapi="getApi" @wfname = "createwfname" :sendData = this.wfname></Search>

          <!---->
          <div class="over"></div>
        </div>
        <!--       <div v-show="isShowSearchWindow">
                 <Search></Search>
               </div>-->

        <div v-show="isShowSearchWindow">
          <Search></Search>
        </div>
        <div v-show="isShowUploadWindow">
          <FileUpload></FileUpload>
        </div>

        <div class="bpmnContainer">
          <div id="BpmnCanvas" ref="canvas"></div>

          <div id="selectApi" ref="canvas" v-show="isShowSelectAPI">
            <div style="height: 20px"></div>
            <el-cascader
                ref="cascaderAddr"
                :options="options"
                v-model="selectedOptions"
                @change="handleChange">
              <!--            api-->
            </el-cascader>
            <div style="height: 20px"></div>
            <el-button @click="sure">api</el-button>
            <!--            data-->
            <!--            -->
<!--            <el-button @click="transferDict"></el-button>-->
            <div style="height: 20px"></div>
            <div>
              <label>api:</label>
              <div style="height: 20px"></div>
              <div>
                <Message>{{ msg }}</Message>
              </div>
            </div>
          </div>

          <div id="BpmnProperties" ref="canvas"></div>
          <!--        api-->
          <Drawer
      title="Create"
      v-model="serviceTask"
      width="720"
      :mask-closable="false"
    >
      <Form :model="formData">
        <FormItem label="" label-position="top">
          <Input v-model="formData.type" disabled />
        </FormItem>
        <FormItem label="" label-position="top">
          <Input v-model="formData.name" placeholder></Input>
        </FormItem>

        <FormItem label="URL" label-position="top">
          <Input v-model="formData.url" placeholder></Input>
        </FormItem>

        <FormItem label label-position="top">
          <RadioGroup>
            <Radio label="get">
              <span>GET</span>
            </Radio>
            <Radio label="post">
              <span>POST</span>
            </Radio>
            <Radio label="put">
              <span>PUT</span>
            </Radio>
            <Radio label="delete">
              <span>DELETE</span>
            </Radio>
          </RadioGroup>
        </FormItem>

        <FormItem label="Header" label-position="top">
          <Input v-model="formData.url" placeholder></Input>
        </FormItem>

        <FormItem label="" label-position="top">
          <Input
            type="textarea"
            v-model="formData.desc"
            :rows="4"
            placeholder
          />
        </FormItem>
      </Form>
      <div class="demo-drawer-footer">
        <Button style="margin-right: 8px" @click="serviceTask = false"
          >Cancel</Button
        >
        <Button type="primary" @click="serviceTask = false">Submit</Button>
      </div>
    </Drawer>
        </div>

      </el-main>
    </el-container>
  </div>
</template>


<script>
import {CustomModeler as BpmnModeler} from "../components/customBpmn"//"bpmn-js/lib/Modeler";
//import BpmnModeler from "bpmn-js/lib/Modeler";
//
import propertiesPanelModule from 'bpmn-js-properties-panel'
import propertiesProviderModule from 'bpmn-js-properties-panel/lib/provider/camunda'
import Search from '../components/Search'
import FileUpload from '../components/FileUpload'
//
import fileLoader from "../components/Editor/fileLoader.vue";
import {request} from '../network/request'
//
import lintModule from "bpmn-js-bpmnlint";
import bpmnlintConfig from "../bpmn/lint/.bpmnlintrc";
//Bpmn
import BlankStr from "../bpmn/samples/blank.js";
import qs from 'qs';
import axios from 'axios';

export default {
  name: "Editor",
  data: function () {
    return {
      serviceTask:false,
      formData: {
        name: "",
        type: ""
      },
      xmlStr: this.$store.state.BpmnXml,
      isCollapse: false,
      bpmnModeler: null,
      container: null,
      isShowSearchWindow: false,
      isShowUploadWindow: false,
      isShowSelectAPI: false,
      popup: 0,
      dialogFormVisible: false,
      labelPosition: 'right',
      wfname:"",
      batchForm: [
        {
          name: "",
          value: "",
          info: "",
          type: "",
          belongTo:"",
          index: 0,
        }
      ],
      batchFormNum: 0,
      msg: [],
      tem: [],
      recomapi: [],
      selectedOptions: "",
      options: [],
      proName: "",
      apiDict: {},
    };
  },
  components: {
    fileLoader: fileLoader,
    Search, FileUpload
  },

  methods: {
    showpopup() {
      this.popup = 1;
      this.isShowSelectAPI = true;
    },
    //
    recevieChildParam(popup, xmlStr) {
      this.popup = popup;
      this.xmlStr = xmlStr;
      this.bpmnModeler.importXML(xmlStr);
    },
    showUploadWindow() {
      this.isShowUploadWindow = !this.isShowUploadWindow
      //this.isShowSearchWindow = false
    },
    showSearch() {
      this.isShowSearchWindow = !this.isShowSearchWindow
      this.isShowUploadWindow = false
    },
    showWindow() {
      //const axios = this.$axios;
      //axios.defaults.withCredentials = true
      this.isShowSelectAPI = false
      this.$prompt('', ':', {
        confirmButtonText: '',
        cancelButtonText: '',
        closeOnClickModal: false,
        showInput: true,
        inputPlaceholder: "",
        inputType: "textarea"
      }).then(({value}) => {
        this.$message({
          type: 'success',
          message: ': ' + (value == null ? "" : value)
        })
        //
        request({

          url: "http://lxc-backend-java.ingress.isa.buaanlsde.cn/recommendation/RecommSemantics",
          method: "get",
          params: {
            message: value
          }
        }).then(res => {
          //console.log(res.data)
          this.xmlStr = res.data;
          this.bpmnModeler.importXML(this.xmlStr);
        }).catch(err => {
          console.log("")
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      })
    },
    // 
    initBpmn: function () {
      this.bpmnModeler = new BpmnModeler({
        container: "#BpmnCanvas", //
        propertiesPanel: {
          parent: '#BpmnProperties'//
        },
        additionalModules: [
          lintModule,//
          //
          propertiesPanelModule,
          propertiesProviderModule
        ],
        linting: {
          bpmnlint: bpmnlintConfig
          // active: getUrlParam('linting')
        }
      });
      this.bpmnModeler.importXML(this.xmlStr); //
      this.addEventBusListener();    //
    },

    // 
    //click()fileLoader
    handleImportButton: function () {
      const element = document.getElementById("fileLoader");
      element.click();
    },
    //loadFile
    //loadFileloadBpmnFromFile
    loadBpmnFromFile: function (bpmnFile) {
      this.xmlStr = bpmnFile;
      this.bpmnModeler.importXML(this.xmlStr);
    },

    //BPMN.BPMNxml
    downloadBpmnToFile: function () {
      this.bpmnModeler.saveXML(
          {
            format: true
          },
          (err, xml) => {
            this.download("1.bpmn", xml);
          }
      );
    },
    //BPMN.SVG
    downloadBpmnToSVG: function () {
      this.bpmnModeler.saveSVG(
          {
            format: true
          },
          (err, data) => {
            this.download("bpmn.svg", data);
          }
      );
    },
    //
    download: function (filename, text) {
      let element = document.createElement("a");
      element.style.display = "none";
      element.setAttribute(
          "href",
          "data:text/plain;charset=utf-8," + encodeURIComponent(text)
      );
      element.setAttribute("download", filename);
      document.body.appendChild(element);
      element.click();
      document.body.removeChild(element);
    },

    //
    //this.xmlStr
    resetBPMN: function () {
      this.xmlStr = BlankStr;
      this.bpmnModeler.importXML(this.xmlStr);
      this.wfname= "";
    },

    //
    saveBPMN: function () {

      this.bpmnModeler.saveXML(
          {
            format: true
          },
          (err, xml) => {
            this.xmlStr = xml;
            this.$store.dispatch("editXml", this.xmlStr);

            this.$prompt('', ':', {
              confirmButtonText: '',
              cancelButtonText: '',
              closeOnClickModal: false,
              showInput: true,
              inputPlaceholder: "",
              inputType: "textarea"
            }).then(({value}) => {
              //
              request({

                url: "https://lxc-backend-java.ingress.isa.buaanlsde.cn/recommendation/saveBPMN",
                method: "post",
                /*params: {
              name:value,
             //message : xml
          },*/
                data: qs.stringify({name: value, message: xml}),


              }).then(res => {
                //console.log(res.data)
                if (res.data === "0") {
                  this.$message({
                    type: 'success',
                    message: ''
                  });
                }
                //this.xmlStr = res.data;
                //this.bpmnModeler.importXML(this.xmlStr);
              }).catch(err => {
                console.log("")
              })
            }).catch(() => {
              this.$message({
                type: 'info',
                message: ''
              });
            })

          }
      );
    },
    //
    checknature: function () {
      var nature = {};
      // console(batchFormNum);
      for (var i = 0; i < this.batchForm.length; i++) {
        nature[this.batchForm[i].name] = this.batchForm[i].value
      }
      this.$alert(JSON.stringify(nature), '', {
        confirmButtonText: '',
        callback: action => {

        }
      });

    },


    resetForm: function () {
      for (var i = 1; i <= this.batchFormNum; i++) {
        this.batchForm.splice(i);
      }
      this.batchForm[0].name = "";
      this.batchForm[0].value = "";
      this.batchForm[0].index = 0;
      this.batchFormNum = 0;
    },

    createwfname(msg){
      this.wfname=msg
      console.log(this.wfname)
    },

    plan: function () {
      this.bpmnModeler.saveXML(
              {
                format: true
              },
              (err, xml) => {
                this.xmlStr=xml;
              }
      );
      this.dialogFormVisible = false;
      var nature = {};
      for (var i = 0; i < this.batchFormNum; i++) {
        var tmpjson = {}
        if( nature.hasOwnProperty(this.batchForm[i].belongTo)){
          tmpjson = nature[this.batchForm[i].belongTo]
        }
        tmpjson[this.batchForm[i].name]=this.batchForm[i].value
        nature[this.batchForm[i].belongTo] = tmpjson
      }
      console.log(nature)
      axios.post("/api/hnust/getDict", {apiDict: this.apiDict, parameterJson: nature})
      axios.get("api/hnust/getAns").then(res=>{
        console.log(res.data)
        axios.post("/api/runwf",{data:res.data,name:this.wfname}).then(res1=>{
          console.log(res1.data)
        //   axios.get("/api/tt").then(res1=>{
            axios.post("/api/test", {xml: this.xmlStr}, {headers: {"Content-Type": "application/xml"}}).then(res2 => {
              console.log(res2.data)
              this.$router.push({
                path: '/graph',
                query: {
                  xml: this.xmlStr,
                  place: res2.data,
                  bpmn_name:res1.data
                }
              })
            })
          })
      })

      //window.open('https://scheme-generation.ingress.isa.buaanlsde.cn/tt')
      // window.open('https://argo-serve.ingress.isa.buaanlsde.cn/workflows/argo/')
    },

    batchAdd(index, length) {
      if (this.batchForm[index].value !== "" && this.batchForm[index].name !== "") {
        this.batchForm.push({name: "", value: "", index: length + 1});
        this.batchFormNum = length + 1;
      } else if (this.batchForm[index].name === "") {
        this.$message.error("");
      } else {
        this.$message.error("");
      }
    },
    batchDel(index) {
      if (this.batchForm.length <= 1) {
        this.batchForm[index].value = "";
        this.batchForm[index].name = "";
        this.batchForm[index].index = 0;
        this.batchFormNum = 0;
      } else {
        //
        this.batchForm.splice(index, 1);
        let num = [];
        //,
        this.batchForm.forEach((item) => {
          num.push(item.index);
        });
        //batchFormNum
        this.batchFormNum = Math.max(...num);
      }
    },

    //

    recoBPMN: function () {

      this.isShowSelectAPI = false

      const axios = this.$axios;
      axios.defaults.withCredentials = true
      var strXML;
      //Vue.prototype.$qs = qs
      this.bpmnModeler.saveXML(
          {
            format: true
          },
          (err, xml) => {
            strXML = xml
          }
      );
      axios({
        method: "post",
        url: "http://lxc-backend-java.ingress.isa.buaanlsde.cn/recommendation/RecommStructure",
        //url: "http://backend-java:8090/recommendation/RecommStructure",
        //url: "http://localhost:8090/recommendation/RecommStructure",
        data: qs.stringify({lxccontent: strXML})
      })
          .then(res => {
            //console.log(content);
            this.xmlStr = res.data;
            this.bpmnModeler.importXML(this.xmlStr);


          })
    },

    //
    checkBPMN: function () {
      /*Toggle Lint*/
      const bjslButton = document.querySelector(".bjsl-button");
      bjslButton.click();
    },
    //
    /*
    textTransform: function() {
      const _bpmnModeler = this.bpmnModeler;
      const axios = this.$axios;
      this.$msgbox({
        title: "NLP",
        message: "",
        showInput: true,
        inputType: "textarea",
        confirmButtonText: "BPMN",
        beforeClose: (action, instance, done) => {
          //
          if (action === "confirm") {
            instance.confirmButtonLoading = true;
            instance.confirmButtonText = "...";
            //
            let _param = new URLSearchParams();
            _param.append(
              "str",
              instance.inputValue ? instance.inputValue : " "
            );
            // _param.append("name", "cbaymax")

            axios({
              method: "post",
              // test url: 'http://123.207.143.93:8081/anything',
              url: "/api/text2bpmn",
              data: _param
            })
              .then(res => {
                console.log(res);
                _bpmnModeler.importXML(res.data);
                instance.confirmButtonLoading = false;
                this.$message({
                  type: "success",
                  message: ""
                });
                done();
              })
              .catch(err => {
                instance.confirmButtonLoading = false;
                this.$message({
                  type: "error",
                  message: err
                });
                done();
              });
          } else {
            this.$message({
              type: "info",
              message: ""
            });
            done();
          }
        }
      });
    }
    */

    //
    textTransform: function () {
      const _bpmnModeler = this.bpmnModeler;
      const axios = this.$axios;
      this.$msgbox({
        title: "NLP",
        message: "",
        showInput: true,
        inputType: "textarea",
        confirmButtonText: "BPMN",
        beforeClose: (action, instance, done) => {
          //
          if (action === "confirm") {
            instance.confirmButtonLoading = true;
            instance.confirmButtonText = "...";
            //
            let _param = new URLSearchParams();
            _param.append(
                "str",
                instance.inputValue ? instance.inputValue : " "
            );
            // _param.append("name", "cbaymax")

            axios({
              method: "post",
              // test url: 'http://123.207.143.93:8081/anything',
              url: "/api/text2bpmn",
              data: _param
            })
                .then(res => {
                  console.log(res);
                  _bpmnModeler.importXML(res.data);
                  instance.confirmButtonLoading = false;
                  this.$message({
                    type: "success",
                    message: ""
                  });
                  done();
                })
                .catch(err => {
                  instance.confirmButtonLoading = false;
                  this.$message({
                    type: "error",
                    message: err
                  });
                  done();
                });
          } else {
            this.$message({
              type: "info",
              message: ""
            });
            done();
          }
        }
      });
    },
    // api
    // api
    getApi(msg) {
      this.options = []
      this.recomapi = []
      let i = 0;
      if (msg[0].length > 0) {
        console.log(msg[0].length);
        this.recomapi = msg[0]
      } else {
        this.recomapi = msg
      }
      // let recomList=[];
      console.log("recomapi is:", this.recomapi)
      for (; i < this.recomapi.length; i++) {
        // i++
        let item = this.recomapi[i]
        console.log("item is:", item)
        if (item.name != null) {
          this.options.push({value: i, label: item.name})
        }
      }
    },
    // api
    handleChange(val) {
      this.$refs['cascaderAddr'].getCheckedNodes()[0].pathLabels
      console.log(this.$refs['cascaderAddr'].getCheckedNodes()[0].value)
      this.tem = this.recomapi[this.$refs['cascaderAddr'].getCheckedNodes()[0].value]
      // axios.post("/api/hnust/CreateBPMN",this.msg,{responseType:'blob'}).then(res=>{const binaryData = [];
      //   binaryData.push(res.data);//
      //   this.bpmn=new Blob(binaryData,{type: "application/x-javascript"} );
      // })
    },
    // api
    sure() {
      var simpleAPI = {}
      simpleAPI["chosen"] = this.proName
      simpleAPI["API "] = this.tem.name
      simpleAPI["API "] = this.tem.content
      this.msg = simpleAPI
      if (this.proName != "" && this.proName != undefined) {
        // console.log("this.proName is:",this.proName)
        this.$set(this.apiDict, this.proName, this.tem)
        // console.log("api ",this.apiDict)
      } else {
        this.$message({
          type: "info",
          message: ""
        });
      }
    },
    // api
    transferDict() {
      axios.post("/api/hnust/getDict", this.apiDict)
    },
    //
    addEventBusListener() {
      console.log('345')
      let that = this
      const eventBus = this.bpmnModeler.get('eventBus')
      const eventTypes = ['element.click']
      eventTypes.forEach(function (eventType) {
        eventBus.on(eventType, function (e) {
          if (!e || e.element.type === 'bpmn:process') {
            console.log("false");
            
            return
          } else {
            console.log(e.element)
            console.log(e.element.id)
            that.proName = e.element.businessObject.name
            var showInfo={}
            showInfo[""]=that.proName
            showInfo["api"]=that.apiDict[that.proName].name
            showInfo["api"]=that.apiDict[that.proName].content
            //console.log(showInfo)
            that.msg=showInfo
            
          }
        })
      })
    },
    // 

    pmrun: function () {
      this.dialogFormVisible = true;
      this.resetForm()
      // resetForm();
      // this.batchForm = [{name:"1",value: "",index: 0},{name:"2",value: "",index: 1}]
      var transParm = "";
      var index = 0;
      // var parajson = this.recomapi[0].apiparams;

      for (var i = 0; i < this.recomapi.length; i++) {
        if(this.recomapi[i].name == null){
          var tmpres = this.recomapi[i].result;
          transParm+=tmpres;
        }
        else{
          console.log(this.recomapi[i].name)
          console.log(this.recomapi[i].apiparams)
          var tmp = JSON.parse(this.recomapi[i].apiparams);
          for (var j = 0; j< tmp.required.length; j++) {
            var param = tmp.required[j];
            if(transParm.match(param.name) == null){
              if(index == 0)
                this.batchForm.pop();
              if (param.value == "")
                this.batchForm.push({name: param.name, value: "", info: param.description, type: param.paramType, belongTo: this.recomapi[i].name,index: index});
              else
                this.batchForm.push({
                  name: param.name,
                  value: param.value,
                  info: param.description,
                  type: param.paramType,
                  belongTo: this.recomapi[i].name,
                  index: index
                });
              index = index + 1;
            }
          }
        }
      }
      this.batchFormNum = this.batchForm.length;
      //var parajson = "{\"required\":[{\"name\":\"latitude\",\"value\":\"33.775867\",\"paramType\":\"STRING\",\"condition\":\"required\",\"description\":\"\"},{\"name\":\"longitude\",\"value\":\"-84.39733\",\"paramType\":\"STRING\",\"condition\":\"required\",\"description\":\"\"},{\"name\":\"from_date\",\"value\":\"2021-10-07\",\"paramType\":\"STRING\",\"condition\":\"required\",\"description\":\"\"},{\"name\":\"to_date\",\"value\":\"2021-10-07\",\"paramType\":\"STRING\",\"condition\":\"required\",\"description\":\"\"},{\"name\":\"elevation\",\"value\":\"166\",\"paramType\":\"STRING\",\"condition\":\"required\",\"description\":\"\"},{\"name\":\"time\",\"value\":\"12:00:00\",\"paramType\":\"STRING\",\"condition\":\"required\",\"description\":\"\"}]}";
      // var parameters = JSON.parse(parajson);
      // this.batchFormNum = parameters.required.length;
      // for (var i = 0; i < parameters.required.length; i++) {
      //   var param = parameters.required[i];
      //   // var info = "paramType:"+param.paramType+";description:"+param.description;
      //   if (i == 0)
      //     this.batchForm.pop();
      //   if (param.value == "")
      //     this.batchForm.push({name: param.name, value: "", info: param.description, type: param.paramType, index: i});
      //   else
      //     this.batchForm.push({
      //       name: param.name,
      //       value: param.value,
      //       info: param.description,
      //       type: param.paramType,
      //       index: i
      //     });

    },
    pmplan: function () {
      // var nature = {};
      // for (var i = 0; i < this.batchFormNum; i++) {
      //   nature[this.batchForm[i].name] = this.batchForm[i].value
      // }

      var nature = {};
      for (var i = 0; i < this.batchFormNum; i++) {
        var tmpjson = {}
        tmpjson[this.batchForm[i].name] = this.batchForm[i].value
        nature[this.batchForm[i].belongTo] = tmpjson
      }
      // var parameterJson = JSON.stringify(nature);
      axios.post("/api/hnust/getDict", {apiDict: this.apiDict, parameterJson: nature})
      axios.get("api/hnust/getAns").then(res=>{console.log(res.data)})
    }
  },


  // - , Vue  DOM created()
  mounted: function () {
    this.initBpmn();
  },
  activated: function () {
    this.xmlStr = this.$store.state.BpmnXml;
    this.bpmnModeler.importXML(this.xmlStr);
  }
}
</script>

